.logo {
  position: absolute;
  top: 0;
  left: 0;

  display: flex;

  width: 137px;
  height: 48px;

  background-color: white;

  align-items: center;
  justify-content: center;

  @media (min-width: $md) and (max-width: $lg - 1) {
    width: 288px;
    height: 72px;
  }
  @media (min-width: $lg) and (max-width: $xl - 1) {
    width: 260px;
    height: 112px;
  }
  @media (min-width: $xl) {
    top: 9px;

    width: 364px;
    height: 112px;
  }

  &__img {
    width: 98px;
    height: 12px;

    @media (min-width: $md) {
      width: 145px;
      height: 18px;
    }
  }
}

.user-content {
  @include font(16px, 400, 24px);

  color: $black-green;

  h1 {
    margin-bottom: 25px;
  }
  h2,
  h3,
  h4 {
    font-weight: 500;

    margin-top: 24px;
    margin-bottom: 24px;
  }

  ul,
  ol {
    margin-top: 24px;
    margin-bottom: 24px;
    padding-left: 28px;
  }

  p:not(:last-of-type) {
    margin-bottom: 20px;
  }
}

.custom-scroll {
  overflow-y: auto;

  &::-webkit-scrollbar {
    width: 4px;

    border-radius: 4px;
    background-color: #f6f5f4;
  }
  &::-webkit-scrollbar-thumb {
    border-radius: 4px;
    background: #b3b2b1;
  }
}
